.signup-container {
    position: relative;
    color: white;
    left: 38%;
    width: 24%;
    height: 10%;
    margin-top: 10%;
    background-color: 352f44;
    padding: 1rem;
    border-radius: 0.5rem;
    background-color: #352f44;

    h2 {
        font-size: 3rem;
        margin-top: 0;
        color: #ffffff;
        font-family: 'Courier New', Courier, monospace;
    }

    form {
        margin: 0;
        align-items: start;
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        .form-group {
            margin-bottom: 2rem;
            width: 98%;
            label {
                font-size: 2rem;
                font-family: 'Courier New', Courier, monospace;
                font-weight: 600;
                padding-right: 1rem;
                width: 100%;
                align-items: start;
                display: flex;
                flex-wrap: wrap;
                column-gap: 2rem;
    
                @media screen and (max-width: 700px) {
                    font-size: 1rem;
                }
            }
    
            input {
                width: 100%;
                height: 2rem;
                font-size: 1.5rem;
                border-radius: 1rem;
                padding: 4px;
            }
        }
            
        button {
            width: 100%;
            border-radius: 0.5rem;
            height: 2rem;
            background-color: #5c5470;
            color: #ffffff;
            border: none;
            padding: 0;
            border: 2px solid #dbd8e3;
            font: inherit;
            cursor: pointer;
            outline: inherit;
            font-family: 'Courier New', Courier, monospace;

        }

        button:hover{
            background-color:#c0b6db;
            color: black;
        }

        button:focus{
            background-color:#dbd8e3;
            color: black;
        }

    }
}
@media screen and (max-width: 700px) {
    .signup-container {
        width: 90%;
        left: 1%;
    }
}